import * as React from "react";
import { Footer } from "../../components/Footer";
import { Header } from "../../components/Header";
import { PageIntro } from "../../components/PageIntro";
import { LayoutContainer } from "../../components/LayoutContainer";
import { graphql, useStaticQuery } from "gatsby";

const PrivacyPage = () => {
  const rawData = useStaticQuery(graphql`
    query {
      sanityPrivacyPolicyPage {
        body {
          children {
            text
          }
        }
        lastUpdated(formatString: "dddd DD MMMM YYYY")
      }
    }
  `);

  const data = rawData.sanityPrivacyPolicyPage

  return (
    <>
      <Header hasPageIntro={true} />
      <PageIntro
        title="Privacy Policy"
        subTitle={`Last updated: [${data.lastUpdated}]`}
      />
      <LayoutContainer className="py-10 sm:py-20">
        <code
          className="space-y-10"
          dangerouslySetInnerHTML={{
            __html: data.body.map((item: any) => item.children.map((item: any) => item.text).join("")).join(""),
          }}
        />
      </LayoutContainer>
      <Footer />
    </>
  );
};

export default PrivacyPage;
